@import "../../common/styles/variables";
@import "../../common/styles/mixins";

.header_container {
	@include flex(center, row, center);
	z-index: 10;
	width: 100%;
	background-image: url('../../assets/texture.jpg');
	height: 80px;
	position: fixed;
	text-align: center;
	box-shadow: 0 3px 15px $textColor;
	h1 {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: $hoverColor;
	}
	.header_sort-panel {
		@include flex(flex-end, row, center);
		position: relative;
		width: 1024px;
		text-align: right;
		margin-right: 10px;
	}
	@media screen and (max-width: 450px) {
		h1 {
			left: 5%;
			transform: translate(0, -50%);
		}
	}
}
